* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body {
  background-color: rgba(224, 224, 224, .863);
}

.search-form {
  height: 55px;
  width: 100%;
  background: radial-gradient(circle, #3f5efb 0%, #fc466b 100%);
  justify-content: center;
  padding: 10px;
  display: flex;
  position: fixed;
}

button[type="submit"] {
  color: #010101;
  background: #fff;
  border-radius: 90px;
  margin-left: 5px;
  padding: 10px;
  transition: all .3s ease-in-out;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
}

button[type="submit"]:hover {
  background: #27ee03;
  font-size: 14px;
}

.gallery {
  object-fit: cover;
  color: #000;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 70px;
  display: flex;
}

.photo-card {
  border: 1px solid #d3d3d3;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 2px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.1);
}

.info {
  color: #000;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 12px;
  font-weight: normal;
}

span {
  font-size: 13px;
  font-weight: bold;
}

.load-more {
  color: #000;
  background: radial-gradient(circle, #3f5efb 0%, #fc466b 100%);
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px;
  transition: all .3s ease-in-out;
  display: block;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
}

.btn-rounded-fill {
  width: 30px;
  height: 30px;
  background-color: #2a292b;
  border-radius: 25px;
  margin-right: 30px;
}

